import React, { useEffect, useRef, useState } from 'react';
import { ICanvasWindow, IInjectorApi, IWebswingInstance } from 'webswing-server-frontend-api/dist/types';
import spinner from './spinner.gif';
import './App.css';
import { getWebswingApi } from './lib/webswing-server-frontend-api/dist';

function App() {
  const wsElementRef = useRef<HTMLDivElement>(null);
  const instanceRef = useRef<IWebswingInstance | null>(null);
  const [loading, setLoading] = useState<boolean>(true);
  const [files, setFiles] = useState<string[] | null>(null);

  useEffect(() => {
    setLoading(true);
    wsInit();
  }, []);

  let docOrigin = window.location.origin;
  if (!window.location.origin) {
    docOrigin = window.location.protocol + "//" + window.location.hostname + (window.location.port ? ':' + window.location.port : '');
  }

  var baseUrl = docOrigin + document.location.pathname;
  baseUrl = baseUrl.indexOf("/", baseUrl.length - 1) !== -1 ? baseUrl : (baseUrl + "/");

  const fileSelected = (file: string) => {
    instanceRef.current!.performAction({actionName: "fileSelected", data: file, binaryData: undefined})
  }

  const wsInit = () => {
    getWebswingApi(baseUrl)
      .then((api) => {
        const instance = api.bootstrap(
          wsElementRef.current!,
          {
            connectionUrl: baseUrl,
            autoStart: true,
            onStart: function() {
              instance.performAction({actionName: "requestFileList", data: undefined, binaryData: undefined})
            },
            windowsListener: {
              windowOpening(htmlOrCanvasWindow) {},
              windowOpened(win) {
                if (win.name != "NbMainWindow") {
                  const winEl = (win as ICanvasWindow).detach();
                  (win as ICanvasWindow).attach(document.querySelector("#container #attach")!);
                } else {
                  const winEl = (win as ICanvasWindow).detach();
                  (win as ICanvasWindow).attach(document.querySelector("#hidden-container")!);
                }
              },
              windowClosing(htmlOrCanvasWindow, windowCloseEvent) {},
              windowClosed(htmlOrCanvasWindow) {},
              windowDocked(htmlOrCanvasWindow) {},
              windowUndocked(htmlOrCanvasWindow) {},
              windowModalBlockedChanged(htmlOrCanvasWindow) {},
            }
          },
          (injector: IInjectorApi) => {
            injector.services.base!.handleActionEvent = (actionName: string, data: string | undefined) => {
              if (actionName == 'fileList') {
                setLoading(false);
                if (data) {
                  setFiles(data.split(";"));
                }
              }
            }
          }
        )
        instanceRef.current = instance;
      })
      .catch((e: any) => {
        console.error(e)
      })
  }

  return <>
    <div className="main-wrapper">
      <div id="nav">
        {loading && 
          <img src={spinner} className="spinner" width="100px" height="100px" />
        }
        {!loading && files && files.length != 0 &&
          files.map(function(file: string) {
            return <div className="file" onClick={() => fileSelected(file)}>
                {file}
              </div>
          })
        }
      </div>
      <div id="container">
        <div id="attach">
        </div>
        <div ref={wsElementRef}
          id="canvas-root"
          className="webswing-element"
          style={{ position: 'absolute', left: 0, top: 0, width: '100%', height: '100%' }}>
        </div>
      </div>
    </div>
    <div id="hidden-container" style={{display: 'none'}}>
    </div>
  </>;
}
export default App;
